.Showcase {
    padding-top: 30px;
    padding-bottom: 30px;
    background: $color-navy url("/img/Ekran Resmi 2023-01-10 17.27.47.png") no-repeat center center / cover;
    color: white;
    text-align: center;

    @include mq("tablet") {
        padding-top: 80px;
        margin-bottom: 100px;
    }

    &-title {
        @include mq("tablet") {
            font-size: 3em;
        }
    }

    &-subtitle {
        margin-top: 10px;

        @include mq("tablet") {
            font-size: 1.4em;
        }
    }

    &-shortlink {
        margin-top: 40px;

        @include mq("tablet") {
            margin-top: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        a {
            display: block;
            color: inherit;

            &+a {
                margin-top: 10px;

                @include mq("tablet") {
                    margin-top: 0;
                }
            }

            @include mq("tablet") {
                margin-left: 10px;
                margin-right: 10px;

            }

            &:hover {
                text-decoration: underline;
            }
        }
    }
    &-store{
        margin-top: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        &-link{
            display: flex;
            align-items: center;
            justify-content: center;
            height: 45px;
            padding-left: 20px;
            padding-right: 20px;
            background-color: rgba(black, .15);
            margin-left: 10px;
            margin-right: 10px;
            border-radius: $border-radius;
            @include mq("tablet") {
                height: 50px;
            }
            &:hover {
                background-color: rgba(black, .3);
            }
            img{
                width: 140px;
            }
        }
    }


}

.Search {
    text-align-last: left;
    margin-top: 30px;
    background-color: $color-yellow;
    padding: $gutter;
    box-shadow: 0 20px 40px rgba(black, .2);

    @include mq("tablet") {
        padding: 50px;
        margin-top: 50px;
        margin-left: 100px;
        margin-right: 100px;
    }

    &-form {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @include mq("tablet") {
            flex-direction: row;
        }

        &-item {
            width: 100%;

            @include mq("tablet") {}

            &+& {
                margin-top: 20px;

                @include mq("tablet") {
                    margin-top: 0;
                    flex-grow: 1;
                    margin-left: 10px;
                }
            }

            &-submit {
                display: flex;
                flex-direction: column;
            }
        }
    }

    .for-work {
        display: flex;
        align-items: center;
        font-size: $font-size-small;
        margin-bottom: 10px;
        justify-content: center;

        @include mq("tablet") {
            position: absolute;
            top: calc(#{$finger-size} + 20px);
            right: 0;
        }

        .checkbox {
            margin-left: 10px;
        }
    }
}

.Highlights {
    overflow: hidden;
    margin-bottom: 100px;
    margin-top: 30px;

    @include mq("tablet") {
        margin-top: -100px;
        overflow: visible;
    }

    &-slider {

        .flickity-viewport {
            overflow: visible;

            @include mq("tablet") {


            }
        }
    }

    &-item {
        width: 30%;
        height: 200px;
        margin-right: 30px;
        background-color: white;
        box-shadow: 0 5px 30px rgba(black, .1);

        @include mq("tablet") {
            width: 300px;
        }

        &.is-expanded {
            width: 80%;


            @include mq("tablet") {
                width: 630px;
            }
        }


        &-show {
            width: 300px;
            height: 100%;

            background-size: cover;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            color: white;
            background-image: url("https://images.unsplash.com/photo-1429497419816-9ca5cfb4571a?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=400&q=80");

            &::before {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 300px;
                height: 100%;
                background-image: linear-gradient(to bottom left, rgba(rgba(24, 175, 220, 0.388), .5), rgba(black, .5));
            }

            &-text {
                position: absolute;
                right: 0;
                top: 0;
                color: $color-navy;
                display: flex;
                flex-direction: column;
                justify-content: center;

                &-alignment {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    margin-left: auto;
                    margin-right: auto;
                    height: 150px;
                    width: 250px;
                }
            }

            .content {
                position: relative;
                z-index: -1;
                padding: 20px;
                z-index: 5;
            }
        }

        &-text {
            &::before {
                background-color: #fff;
            }
        }

        &-extra {
            display: none;
        }
    }
}

h4,
h5 {
    margin: 0;
}

.button-properties {
    font-weight: normal;
    width: 170px;
}


.icon-date {
    margin-top: 10px;
    margin-bottom: 10px;
}

.left-text {

        height: 100%;
        width: 300px;
        color: $color-navy;
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
}

.center {
    margin-left: auto;
    margin-right: auto;
    height: 150px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.icon-calendar-date {
    height: 17px;
    width: 17px;
}
