$Header-height: 68px;

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Header {
    height: $Header-height;

    .container {
        display: flex;
        height: 100%;
        align-items: center;
        position: relative;
    }

    &-switch {
        position: relative;
        left: 10px;
        margin-left: auto;
        background: none;
        border: 0;

        @include mq("tablet") {
            display: none;
        }
    }

    &-menu {
        z-index: 2;
        display: flex;
        pointer-events: none;
        opacity: 0;
        //transform: translatex(100%);
        transition: .2s;
        position: absolute;
        left: 0;
        right: 0;
        top: $Header-height;
        padding-bottom: 20px;
        padding-left: $gutter;
        padding-right: $gutter;
        flex-direction: column;
        background-color: #fff;
        box-shadow: 0 20px 30px rgba(black, .2);

        .Header.menu-show & {
            opacity: 1;
            // transform: translateY(0);
            pointer-events: auto;
        }

        @include mq("tablet") {
            opacity: 1;
            pointer-events: auto;
            transform: translateY(0);
            transition: 0s;
            display: flex;
            margin-left: auto;
            flex-direction: row;
            position: static;
            box-shadow: none;
            background-color: transparent;
            padding-left: 0;
            padding-right: 0;
            padding-bottom: 0;
        }

        &-user {
            margin-right: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            @include mq("tablet") {
                flex-direction: row;
            }

            a {
                width: 100%;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                padding-left: 20px;
                padding-right: 20px;
                height: $finger-size;

                &:hover {
                    background-color: #eee;
                }
            }
        }

        &-property {
            margin-top: 20px;
            justify-content: center;

            @include mq ("tablet") {
                margin-top: 0;
            }
        }

        &-helper {
            margin-top: 20px;
            display: flex;
            justify-content: center;

            @include mq("tablet") {
                margin-left: 20px;
                margin-top: 0;
            }

            select {
                appearance: none;
                background-color: transparent;
                border: 1px solid #e0e6f1;
                padding-left: 10px;
                padding-right: 10px;
                border-radius: 0;
                color: $color-navy;
                height: $finger-size;

                &:nth-child(1) {
                    border-bottom-left-radius: $border-radius;
                    border-top-left-radius: $border-radius;
                }

                &:nth-child(2) {
                    border-bottom-right-radius: $border-radius;
                    border-top-right-radius: $border-radius;
                }
            }
        }
    }
}

input[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
}